.banner {
  background: #f3f6ef;
  padding-top: 127px;
  padding-bottom: 94px;
  &__title {
    width: 434px;
    color: #2f2a37;
    font-family: "Avenir-bold", sans-serif;
    font-size: 54px;
    font-style: normal;
    font-weight: 800;
    line-height: 115%;
  }
  &__text {
    color: #6e6c72;
    margin-top: 31px;
    /* Text */
    font-family: Avenir, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%; /* 24px */
    width: 432px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  &__button {
    color: var(--ed-space, #2f2a37);
    font-family: Avenir, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 120%; /* 16.8px */
    text-transform: uppercase;
    border-radius: 40px;
    border: 1px solid transparent;
    background: var(
      --gradient,
      linear-gradient(129deg, #6bffca 0%, #00ff47 100%)
    );

    padding: 19px 27px;
    margin-top: 74px;

    cursor: pointer;
    transition: 0.3s;
    &:hover {
      border: 1px solid rgba(0, 0, 0, 0.12);
      box-shadow: 0px 1px 14px 0px rgba(0, 0, 0, 0.12);
    }
  }
  .img-absolute {
    position: absolute;
    top: -10%;
    right: -50%;
    border-radius: 821px;
    background: var(--accent, #6bffca);
    filter: blur(120px);
    width: 821px;
    height: 821px;
  }
}
